/* eslint-disable */
import { getActiveElement } from "@testing-library/user-event/dist/utils";
import React, { useEffect, useState } from "react";
import { propTypes } from "react-bootstrap/esm/Image";
import { useHistory, useParams } from "react-router-dom";
import styled from "styled-components";
import "./Detail.scss";

let 박스 = styled.div`
  padding: 20px;
`;
let 제목 = styled.h4`
  font-size: 25px;
  color: ${(props) => props.색상};
`;

function Detail(props) {
  

  let { id } = useParams();
  let imgNum = parseInt(id);

  let history = useHistory();
  let 찾은상품 = props.shoes.find(function (상품) {
    return 상품.id == id;
  });
  const [showModal, setShowModal] = useState(true);
  const [inputData, inputData변경] = useState('');
  // useEffect(() => {
  //   let 타이머 = setTimeout( ()=>{ showModal(true); }, 2000);
  //   // return function 어쩌구(){실행할코드}
  // });
  useEffect(() => {
    const 타이머 = setTimeout(() => {setShowModal(false)}, 2000);
    return ()=>{ clearTimeout(타이머)}
},[]);

function 재고빼기(){
  let new재고 = [ ...props.재고 ];
  new재고[0]--;
  props.재고변경(new재고);


}





  return (
    <div className="container">
      <div className="row">
        <박스>
          <제목 className="red">Detail</제목>
        </박스>
     {/* <input type="text" onChange={(e)=>{inputData변경(e.target.value)}} /> */}
        {
          showModal === true
          ? <Modal />
          : null
        }
        <div className="col-md-6">
          <img src={ 'https://codingapple1.github.io/shop/shoes' + (imgNum+1) + '.jpg' } width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <p>{찾은상품.price}원</p>

         <Infor 재고={props.재고}></Infor>

          <button className="btn btn-danger" onClick={재고빼기}>주문하기</button>&nbsp;
          <button
            className="btn btn-danger"
            onClick={() => {
              history.goBack();
            }}
          >
            뒤로가기
          </button>
        </div>
      </div>
    </div>
  );
}
function Infor(props) {
  return (
    <p>재고 : {props.재고[0]}</p>
  );
}


export default Detail;
function Modal() {
  return (
    <div className="my-alert2">
      <p>재고가 얼마 남지 않았습니다.</p>
    </div>
  );
}
